import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import Header from '../../components/header';
import api from '../../api';
import { FiArrowDownRight, FiArrowRight } from 'react-icons/fi';
import loading from '../../img/loading1.gif';
import './checkNf.css';


export default function CheckNf(props) {

    const history = useHistory();
    const [chave_form, setChaveForm] = useState('');
    const [selectedNFLote, setNfForm] = useState('');
    const [selectedChavNFUsina, setChaveUsina] = useState('');
    const [nfData, setNfData] = useState('');
    const [selectedQtdNFUsina, setQtdUsina] = useState('');
    const salesNf = props.match.params.nfSales;
    const company = props.match.params.company
    const token = localStorage.getItem('token');


    async function handleSubmit(element) {
        element.preventDefault();
        try {
            const invoiceFl = await api.post('nf_submitFL', {
                "company": company,
                "nfSales": salesNf,
                "nfForm": selectedNFLote
            }, {
                headers: {
                    Authorization: token
                }
            });

            if (invoiceFl.data.status == 203) {
                alert(invoiceFl.data.response);
            } else {
                alert(invoiceFl.data.response);
                document.querySelector('#input').value = '';

            }

        } catch (error) {
            console.log(error)
        }


        try {
            const nfUsina = await api.post('nf_submitUsin', {
                "company": company,
                "nfSales": salesNf,
                "qtdUsina": selectedQtdNFUsina,
                "keyUsina": selectedChavNFUsina
            },
                {
                    headers: {
                        Authorization: token
                    }
                });
            if (nfUsina.data.status == 200) {
                alert(nfUsina.data.response);
            }
            else {
                alert(nfUsina.data.response);
            }
        } catch (error) {

            console.log(error.response.data.response);
            alert(error.response.data.response);

        }

        setNfData(200);
    }

    function redirectHome() {
        history.push('/home');
    }
    function redirectCheck() {
        setNfData('');
        history.push(`/checkNf/${salesNf}/${company}`)
    }

    if (nfData === 200) {
        return (
            <div className='orderChk-container'>
                <form>
                    <div className='txtChkQ-box'>
                        <p>Dados inseridos, efetue um refresh no OBF4000 e reenvie a nota, deseja inserir mais notas?</p>
                    </div>
                    <div className='nfChkQ-data-box'>
                        <button onClick={redirectCheck}>Sim</button>
                        <button onClick={redirectHome}>Não</button>
                    </div>
                </form>

            </div>
        )
    }

    return (

        <>
            <Header />
            <div className='orderChk-container'>
                <form onSubmit={handleSubmit}>
                    <div className='txtChk-box'>
                        <p>NF Faturada encontrada, insira os dados abaixo <br /> clique em continuar,
                        após isso acesse OBF4000 e localize a NF emitida, <br />selecione, clique em refresh, e reenvie a NF.
                </p>
                    </div>
                    <div>
                        <div className='nfChk-data-box'>
                            <input type='text' placeholder='Nr NF. form. lote' onChange={(element) => { setNfForm(element.target.value) }}></input>
                            <input type='text' placeholder='Chave form. lote' onChange={(element) => { setChaveForm(element.target.value) }}></input>
                            <input type='text' placeholder='Chave NF usina' onChange={(element) => { setChaveUsina(element.target.value) }}></input>
                            <input type='text' placeholder='Qtd. NF usina' onChange={(element) => { setQtdUsina(element.target.value) }}></input>


                        </div>
                    </div>
                    <footer onClick={handleSubmit}>
                        <button type='submit' >
                            <div className='footer-left'>
                                <p>Continuar</p>
                            </div>
                            <div className='footer-right'>
                                <FiArrowRight size={25} color="white" />
                            </div>
                        </button>
                    </footer>
                </form>
            </div>

        </>
    )
}

