import React from 'react';
import  { BrowserRouter, Switch, Route, Redirect, useHistory } from 'react-router-dom';

import login from './pages/login/login';
import home from './pages/home/home';
import selectNf from './pages/selectNF/selectOrder';
import OrderRequest from './pages/orderRequest/orderRequest';   
import CheckNf from './pages/checkNf/checkNf';
import orderPackage from './pages/orderPackage/orderPackage';
import nfMan from './pages/nfMan/nfMan';
import searchnf from './pages/searchNF/searchNF';
import viewNF from './pages/viewNF/viewNF';
import viewOrder from './pages/viewOrder/viewOrder';

function Routes(){
    return(
        <BrowserRouter>
            <Switch>
                <Route path='/' exact component={login}/>
                <Route path='/home' exact component={home}/>
                <Route path='/selectOrder' exact component={selectNf}/>                
                <Route path='/orderRequest/:order/:company' exact component={OrderRequest}/>                
                <Route path='/checkNf/:nfSales/:company' exact component={CheckNf}/>                
                <Route path='/orderPackage/:order/:company' exact component={orderPackage}/>                
                <Route path='/nfman/:company' exact component={nfMan}/>                
                <Route path='/searchnf' exact component={searchnf}/>       
                <Route path='/viewNF' exact component={viewNF}/>         
                <Route path='/viewOrder' exact component={viewOrder}/>
            </Switch>
        </BrowserRouter>
    )
}

export default Routes;

