import React, { useState } from 'react';
import './login.css';
import logo from '../../img/logo_temp.png';
import { useHistory } from 'react-router-dom';
import api from '../../api';

function App() {

  const history = useHistory();
  const [username, setUser] = useState('');
  const [password, setPass] = useState('');
  const [ErrorLog, setErrorLog] = useState('');

  async function handleSubmit(element) {

    element.preventDefault();

    try {
      const response = await api.post('login', {
        "username": username,
        "password": password
      })

      localStorage.setItem('token', response.data.token);

      return history.push('/home');

    } catch (error) {


      let botao = document.getElementById('button');
      botao.style.marginBottom = '10px';

      return setErrorLog('Usuario ou senha inválidos');
    }

  }


  return (
    <div className="container">
      <div className="left-box">
        <h1 className='first-h1'>Olá!</h1>
        <h1>Bem vindo!</h1>
        <h2> Digite as informações ao lado para acesso</h2>
        <h3>Sistema de fauramento manual Prisma</h3>
      </div>
      <div className='login-box'>
        <form className='action-form' action='post' onSubmit={handleSubmit}>
          <img src={logo} />
          <input type='text' placeholder='Login' onChange={(element) => { setUser(element.target.value) }}></input>
          <input type='password' placeholder='Senha' onChange={(element) => { setPass(element.target.value) }}></input>
          <button id='button' type='subimit'>Login</button>
          <h3>{ErrorLog}</h3>
        </form>
        <div className="help-box"></div>
      </div>
    </div>
  );
}

export default App;
