import React, { useState, useEffect } from 'react';
import Header from '../../components/header';
import { useHistory } from 'react-router-dom';
import api from '../../api';
import './orderPackage.css';
import loading from '../../img/loading1.gif';
import { FiArrowRight, FiAlertCircle } from 'react-icons/fi'

export default function OrderPackage(props) {


    const order = props.match.params.order;
    const company = props.match.params.company;
    const token = localStorage.getItem('token');
    const [sale, setSale] = useState('');
    const [saleStatus, setSaleStatus] = useState('');
    const history = useHistory();

    useEffect(() => {
   
        async function checkSale() {
            const resultSales = await api.post('checkSalesOrder', {
                "company": company,
                "order": order,
            },
                {
                    headers: {
                        Authorization: token
                    }
                })

            setSaleStatus(resultSales.data.status)
   
        } checkSale();



    }, []);


    if (saleStatus === 200) {

        async function setSale() {
            const resultSales = await api.post('checkSalesOrder', {
                "company": company,
                "order": order,
            },
                {
                    headers: {
                        Authorization: token
                    }
                })
            let nfSales = resultSales.data.data
            console.log(resultSales)
            history.push(`/checkNf/${nfSales}/${company}`)
        }

        setSale()
    } else if (saleStatus === 203) {

        return (
            <>
                <Header />
                <div className='orderPkg-container'>
                    <div className='package-box'>
                        <p>Nota ainda não foi faturada, operação cancelada devido ao tempo limite atingido
                        <br />volte a home e tente novamente.
                    </p>
                    </div>
                    <div className='selectionPkg'>
                        <FiAlertCircle size={45} />
                        <div className='push'></div>
                    </div>
                </div>
            </>
        )
    }

    return (

        <>
            <Header />
            <div className='orderPkg-container'>
                <div className='package-box'>
                    <p>Dados da embalagem inseridos, Aguardando o faturamento</p>
                </div>
                <div className='selectionPkg'>
                    <img src={loading} />
                    <div className='push'></div>
                </div>
            </div>
        </>
    )
}