import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom'
import Header from '../../components/header';
import './selectOrder.css';
import api from '../../api';
import { FiArrowDownRight, FiArrowRight } from 'react-icons/fi';

export default function SelectNf() {

    const [order, setOrder] = useState('');
    const company = localStorage.getItem('company');
    const [resultOrder, setResultOrder] = useState();
    const [order_package, setOrderPackage] = useState('');
    const [order_status, setOrderStatus] = useState(0);
    const token = localStorage.getItem('token');
    const [reload, setReload] = useState(false)
    const history = useHistory();
    const [msgBox, setMsgBox] = useState(' ');

    async function handleSubmit(element) {
        element.preventDefault();
        if (order === '') {
            return alert('Informe um pedido de venda')
        }

        try {
            const resultERP = await api.post('orders', {
                "order": order,
                "company": company,
            },
                {
                    headers: {
                        Authorization: token
                    }
                });
            console.log('teste: ', resultERP);
            setResultOrder(resultERP.status)
        } catch (error) {
            setMsgBox(error.response.data.response);
            setResultOrder(error.response.status)
            console.log('Status: ' + error.response.status)
            console.log('Error Log Completo: ' + error);

            if (resultOrder === 404) {
                document.getElementById('checkOrder').style.marginBottom = '10px'
                document.getElementById('order').style.marginTop = '30px'
            }
        }
    }


    function render() {

        if (resultOrder === 200 || resultOrder === 403) {
            history.push(`/orderRequest/${order}/${company}`);
        }
        else {
            return (

                <div className='selOrder-container'>
                    <form action='POST' onSubmit={handleSubmit}>
                        <div className='txt-box'>
                            <p className='first-p'>Empresa selecionada - {company}</p>
                            <p >Digite o pedido ao lado que será necessário informar as notas manualmente</p>
                        </div>
                        <div className='selection'>
                            <p id='order'>Digite o pedido</p>
                            <input id='checkOrder' maxLength='6' placeholder='Pedido' onChange={(element => { setOrder(element.target.value) })}></input>
                            <h3 id='msgBox'>{msgBox}</h3>
                            <div className='push'></div>
                        </div>
                        <footer onClick={handleSubmit}>
                            <button type='submit'>
                                <div className='footer-left'>
                                    <p>Continuar</p>
                                </div>
                                <div className='footer-right'>
                                    <FiArrowRight size={25} color="white" />
                                </div>
                            </button>
                        </footer>
                    </form>
                </div >
            )
        }
    }

    return (
        <>
            <Header />
            {render()}
        </>
    )
}