import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom'
import Header from '../../components/header';
import api from '../../api';
import './nfman.css';

export default function NfMan() {

    const token = localStorage.getItem('token');
    const company = localStorage.getItem('company');
    const [selectedNFSales, setSelectedNFSales] = useState('');
    const [selectedNFLote, setSelectedNFLote] = useState('');
    const [selectedChavNFUsina, setSelectedChavNFUsina] = useState('');
    const [selectedQtdNFUsina, setSelectedQtdNFUsina] = useState('');

    async function handleSubmit() {


        try {
            const invoiceFl = await api.post('nf_submitFL', {
                "company": company,
                "nfSales": selectedNFSales,
                "nfForm": selectedNFLote
            }, {
                headers: {
                    Authorization: token
                }
            });

            if (invoiceFl.data.status == 203) {
                alert(invoiceFl.data.response);
            } else {
                alert(invoiceFl.data.response);
                document.querySelector('#input').value = '';

            }

        } catch (error) {
            console.log(error)
        }


        try {
            const nfUsina = await api.post('nf_submitUsin', {
                "company": company,
                "nfSales": selectedNFSales,
                "qtdUsina": selectedQtdNFUsina,
                "keyUsina": selectedChavNFUsina
            },
                {
                    headers: {
                        Authorization: token
                    }
                });
            if (nfUsina.data.status == 200) {
                alert(nfUsina.data.response);
            }
            else {
                alert(nfUsina.data.response);
            }
        } catch (error) {

            console.log(error.response.data.response);
            alert(error.response.data.response);

        }
    }

    return (
        <>
            <Header />
            <div className='main-container'>
                <di className='left-box'>
                    <p>Insira os dados que deseja</p>
                    <p>inputar manualmente</p>
                </di>
                <di className='right-box'>
                    <input className='input' placeholder='NF Venda' onChange={(element) => { setSelectedNFSales(element.target.value) }}></input>
                    <input className='input' placeholder='NF form. lote' onChange={(element) => { setSelectedNFLote(element.target.value) }}></input>
                    <input className='input' placeholder='Chave NF usina' onChange={(element) => { setSelectedChavNFUsina(element.target.value) }}></input>
                    <input className='input' placeholder='Quantidade NF usina' onChange={(element) => { setSelectedQtdNFUsina(element.target.value) }}></input>
                </di>
                <footer>
                    <p onClick={() => { handleSubmit() }}>CONTINUAR</p>
                </footer>
            </div>
        </>
    )
}