import React, { useEffect, useState } from 'react';
import { Link, useHistory } from 'react-router-dom'
import { FiChevronDown, FiLogOut } from 'react-icons/fi';
import logo from '../img/logo_temp.png';
import './header.css';




function clearData() {
  localStorage.clear();
  sessionStorage.clear();

  
}


function Header() {

  const history = useHistory()
  const company = localStorage.getItem('company');

  function validaClick(){
    history.push(`/nfMan/${company}`)
  }

  return (
    <div className="header">
       <img src={logo}></img>
      <div className="menu-area">       
      <ul>
          <li><Link to="/home">Home</Link></li>
          <li><Link to="#">Notas Fiscais <FiChevronDown /></Link>
            <ul className="dropdown">
              <li><a onClick={()=>{ validaClick()}}>Inserir dados manual</a></li>
              <li><Link to="/viewNF">NFs referenciadas</Link></li>
              <li><Link to="/viewOrder">Pedidos Vs NFs</Link></li>
            </ul>
          </li>
        </ul>
      </div>      
      <div className="user">   
        <div>
          <p>{}</p>
          <p id="selected_company">Empresa {company}</p>
        </div>
        <div>
          <Link to="/" onClick={() => { clearData() }}><p className="exit">Sair <FiLogOut id="exit-icon" /></p></Link>
        </div>
      </div>
    </div>
  );
}

export default Header;