import React, { useState } from 'react';
import api from '../../api';
import Header from '../../components/header';
import './viewNF.css';

export default function ViewNF() {

    const token = localStorage.getItem('token');
    const company = localStorage.getItem('company');
    const [nfSales, setNfSales] = useState('');
    const [nfRefer, setNfRefer] = useState('');
    const [nfUsin, setNfUsin] = useState('');
    const [loading, setLoading] = useState(false);
    let classeLoad = 'container-services-lnone';

    async function handleSubmit() {

        setNfRefer('')
        setNfUsin('');
        setLoading(true);

        const viewNFData = await api.post('nf_submitSearchFL', {
            "nfSales": nfSales,
            "company": company
        },
            {
                headers: {
                    Authorization: token
                }
            });


        const viwNfUsinData = await api.post('nf_submitSearchUs', {
            "nfSales": nfSales,
            "company": company
        },
            {
                headers: {
                    Authorization: token
                }
            });

        setLoading(false)
        return setNfRefer(viewNFData.data.data) , setNfUsin(viwNfUsinData.data.data)
    }

    function renderReferTb() {

        if (nfRefer.length > 0) {

            
            return nfRefer.map(nfReferData => {

                return (
                    <tr>
                        <td>{nfReferData.nota_fiscal_refer}</td>
                        <td>{nfReferData.chav_aces_nf_refer}</td>
                    </tr>
                )
            });
        }
        else {
            return (
                <tr>
                    <td className='empty'>Selecione uma NF</td>
                    <td></td>
                </tr>
            )
        }
    }

    function renderUsinTB(){
 
        if(nfUsin.length>0){
            return nfUsin.map(
                nfUsinData => {
                    return(
                        <tr>
                            <td>{nfUsinData.nota_usina}</td>
                            <td>{nfUsinData.chave_nfe_export}</td>
                            <td>{nfUsinData.qtd_export_item}</td>
                        </tr>
                    )
                }
            )
        }else{
            return(
                <tr>
                    <td>Selecione uma NF</td>
                    <td></td>
                    <td></td>
                </tr>
            )
        }
    }

    if (loading === true) {
        classeLoad = 'container-services-l'
    }

    return (
        <>
            <Header />
            <div className={classeLoad}>
                <div className="lds-dual-ring"></div>
                <br />
            </div>
            <div className='viewNF-container'>
                <div className='searchNF'>
                    <input placeholder='NF venda' onChange={(element) => { setNfSales(element.target.value) }}></input>
                </div>
                <div className='table-nf'>
                    <div>
                        <h1>Notas atreladas a NF: </h1>
                        <table>
                            <thead>
                                <tr>
                                    <td>Nota FOL</td>
                                    <td>Chave NF</td>
                                </tr>
                            </thead>
                            <tbody>
                                {renderReferTb()}
                            </tbody>
                        </table>
                        <table>
                            <thead>
                                <tr>
                                    <td>Nota Usina</td>
                                    <td>Chave NF</td>
                                    <td>Qtd</td>
                                </tr>
                            </thead>
                            <tbody>
                               {renderUsinTB()}
                            </tbody>
                        </table>
                    </div>
                </div>
                <footer>
                    <button type='submit' onClick={() => { handleSubmit() }}>
                        Continuar
                </button>
                </footer>
            </div>
        </>
    )
}