import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom'
import './home.css';
import Header from '../../components/header';
import api from '../../api';
import { FiArrowRight } from 'react-icons/fi';

export default function Home() {

    const [companies, setCompanies] = useState([]);
    const [selectedCompany, setSelectedCompany] = useState('');
    const history = useHistory();
    const token = localStorage.getItem('token');


    //Chamada de API
    useEffect(() => {

        async function companiesFunction() {
            const resultErp = await api.get('companies', {
                headers: {
                    Authorization: token
                }
            });

            setCompanies(resultErp.data.data);
        } companiesFunction();
    }, [token]);

    function handleSubmit(e) {
        e.preventDefault();

        if(selectedCompany === ''){
           return alert('Selecione uma empresa')
        }       
        localStorage.setItem('company', selectedCompany);
        return history.push('/selectOrder');
    }

    function companiesReccords() {
        return (
            companies.map(company => {
                return (
                    <option value={company.cod_empresa}>{company.cod_empresa} - {company.den_reduz}</option>
                )
            })
        )
    }

    return (
        <div className='first'>
            <Header />
            <form action='POST' onSubmit={handleSubmit}>
                <div className='home-container'>
                    <h1> Bem vindo, Selecione as empresa</h1>
                    <div className='home-box'>
                    
                            <select onChange={(element) => {
                                setSelectedCompany(element.target.value);
                                localStorage.setItem('company', element.target.value)
                            }}>
                                <option selected disabled>Selecione a empresa</option>
                                {companiesReccords()}
                            </select>
                    </div>
                </div>
                <footer>
                    <button type='submit'>
                    <div className='footer-left'>
                        <p>Continuar s</p>
                    </div>
                    <div className='footer-right'>
                        <FiArrowRight size={25} color="white" />
                    </div>
                    </button>
                </footer>
            </form>
        </div>
    )
}