import React, { useState, useEffect } from 'react';
import Header from '../../components/header';
import { Redirect } from 'react-router-dom';
import api from '../../api';
import { FiArrowDownRight, FiArrowRight, FiAlertCircle } from 'react-icons/fi';
import loading from '../../img/loading1.gif';
import './orderRequest.css'


export default function OrderRequest(props) {

    const order = props.match.params.order;
    const company = props.match.params.company
    const [order_request, setOrder_request] = useState('');


    useEffect(() => {

        async function companiesFunction() {
            const checkOrder = await api.post('order_request', {
                "order": order,
                "company": company
            })
            setOrder_request(checkOrder.status);

        } companiesFunction();
    }, []);

    if (order_request === 200) {

        return <Redirect to={`/orderPackage/${order}/${company}`} />

    } else if (order_request === 203) {
        return (
            <>
                <Header />
                <div className='orderReq-container'>
                    <div className='txtReq-box'>
                        <p className='loadOrder'>Solicitação de faturamento não gerada a tempo, tente novamente</p>
                    </div>
                    <div className='selectionReq'>
                        <FiAlertCircle size={45} />
                        <div className='push'></div>
                    </div>
                </div>
            </>
        )
    }

    return (
        <>
            <Header />
            <div className='orderReq-container'>
                <div className='txtReq-box'>
                    <p className='loadOrder'>Gere a solicitação de faturamento no Logix e aguarde a atualização</p>
                </div>
                <div className='selectionReq'>
                    <img src={loading} />
                    <div className='push'></div>
                </div>          
            </div>
        </>
    )

}